}

:root[data-theme=dark] {
  color-scheme: dark;
  --body-background-color: var(--brand-blue);
  --text-color: var(--brand-beige);
  --banner-background-color: var(--brand-beige);
  --banner-color: var(--brand-blue);
  --hr-color: var(--brand-beige);
