/*Margin and Padding*/
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, form, fieldset, input, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

/* Normalizes Font-size for headers*/
h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

/* Removes list-style from lists*/
ol, ul {
  list-style: none;
}

/* Normalizes font-style and font-weight to normal */
address, caption, cite, code, dfn, th, var {
  font-style: normal;
  font-weight: normal;
}

/* Removes borders from tables*/
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Removes border from fieldset and img*/
Fieldset, img {
  border: 0;
}

/* Left-aligns text in caption and th */
caption, th {
  text-align: left;
}

/*End Normalizing*/
@media (prefers-reduced-motion: no-preference) {
  @view-transition {
    navigation: auto;
  }
}
@media (prefers-reduced-motion: no-preference) {
  html {
    scroll-behavior: smooth;
  }
}

body {
  background-color: var(--body-background-color);
  color: var(--text-color);
  font-family: "Nunito", sans-serif;
  font-size: 100%;
  line-height: 1.75;
  overscroll-behavior: none;
}
body.no-scroll {
  overflow: hidden;
}

.sr-only:not(:focus):not(:active) {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

h1, h2, h3, h4 {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Lora", serif;
  font-weight: 500;
  letter-spacing: -0.02em;
  line-height: 1.5;
}

h1 {
  font-size: var(--step-4);
  font-weight: 700;
  letter-spacing: 0.01em;
  line-height: 1.25;
  margin-bottom: var(--space-l);
}
.post h1 {
  margin-bottom: 0;
}

h2 {
  font-size: var(--step-3);
  margin-bottom: var(--space-l);
}
* + h2 {
  margin-top: var(--space-xl);
}
.week-of + h2, hr + h2 {
  margin-top: unset;
}
.page-archive * + h2 {
  margin-top: var(--space-4xl);
}
.post-content h2 {
  text-wrap: balance;
  text-wrap: pretty;
}
.post-content h2 span {
  font-size: var(--step--1);
}

h3 {
  font-size: var(--step-2);
}
*:not(h2, hr) + h3 {
  margin-top: var(--space-xl);
}

h4 {
  font-size: var(--step-1);
}

a {
  color: currentColor;
}

strong {
  font-weight: 700;
}

p, ul, ol {
  font-size: var(--step-0);
  line-height: 1.5;
}
* + p, * + ul, * + ol {
  margin-top: var(--space-l);
}
* + p:first-child, * + ul:first-child, * + ol:first-child {
  margin-top: unset;
}
h2 + p, h2 + ul, h2 + ol {
  margin-top: unset;
}

ol {
  list-style-type: decimal;
  margin-left: var(--space-l);
}
ol ::marker {
  margin-right: 1em;
}

ul {
  list-style-type: disc;
  margin-left: var(--space-xs);
}

.lifelist,
#social,
.comment-list {
  list-style-type: "";
  margin-left: 0;
}

.comment-list li + li {
  margin-top: var(--space-2xl);
}

* + .video,
* + .wp-block-embed-youtube,
* + .cp_embed_wrapper {
  margin-top: var(--space-l);
}

hr + p {
  margin-top: unset;
}

* + blockquote {
  margin-top: var(--space-l);
}

* + pre {
  margin-top: var(--space-l);
}

.feed-icon:focus-visible {
  outline: var(--text-color) dotted 2px;
  outline-offset: 4px;
}
.feed-icon:focus {
  outline: var(--text-color) dotted 2px;
  outline-offset: 4px;
}
.feed-icon:focus:not(:focus-visible) {
  outline: none;
}

.social-links {
  display: flex;
  font-size: var(--step-2);
  gap: var(--space-s);
  list-style-type: "";
  margin: 0;
  padding: 0;
}
.site-footer .social-links {
  margin-left: auto;
}
@media (max-width: 41.1875em) {
  .site-header .social-links {
    display: none;
  }
}
.social-links .text {
  font-size: var(--step-0);
}
.social-links a {
  align-items: center;
  border: none;
  display: flex;
  gap: var(--space-2xs);
  outline: 1px solid transparent;
  padding: var(--space-3xs);
  transition: 0.4s ease-out;
}
.social-links a:hover {
  outline-color: currentColor;
}
.social-links a:focus-visible {
  outline-color: currentColor;
}
.social-links a:focus {
  outline-color: currentColor;
}
.social-links a:focus:not(:focus-visible) {
  outline-color: transparent;
}

.site-header {
  background-color: var(--banner-background-color);
  color: var(--banner-color);
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: var(--space-s) var(--space-m);
  position: relative;
  width: 100%;
}
@media (min-width: 60em) {
  .site-header {
    padding: var(--space-m);
  }
}
@media (min-width: 1024px) {
  .site-header {
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: var(--space-s) var(--space-m);
    padding-inline: max(var(--space-m), 50% - 620px);
  }
  .site-header .nav-main {
    grid-column: span 2;
    justify-self: center;
  }
}
.site-header h1 {
  font-size: var(--step-0);
  font-weight: 900;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
}
@media (max-width: 37.4375em) {
  .site-header h1 {
    display: none;
  }
}
.site-header h2 {
  font-size: var(--step--1);
  font-family: "Nunito", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
}
@media (max-width: 37.4375em) {
  .site-header h2 {
    display: none;
  }
}
.site-header .utilities {
  align-items: center;
  display: flex;
  font-size: var(--step--1);
  gap: var(--space-xl);
}
.site-header .theme-picker {
  accent-color: var(--body-background-color);
  color-scheme: dark;
  display: flex;
  gap: var(--space-2xs);
}
[data-theme=light] .site-header .theme-picker {
  accent-color: var(--text-color);
  color-scheme: dark;
}
[data-theme=dark] .site-header .theme-picker {
  accent-color: var(--body-background-color);
  color-scheme: light;
}
@media (prefers-color-scheme: dark) {
  :root:not([data-theme]) .site-header .theme-picker {
    accent-color: var(--body-background-color);
    color-scheme: light;
  }
}
@media (max-width: 56.1875em) {
  .site-header .theme-picker .visible-label {
    display: none;
  }
}
@media (max-width: 56.1875em) {
  .site-header .theme-picker {
    gap: var(--space-3xs);
  }
  .site-header .theme-picker label {
    border: 1px solid currentColor;
    border-radius: 100px;
    font-size: var(--step--2);
    padding: 0 var(--space-2xs);
    position: relative;
  }
  .site-header .theme-picker label:has(input:checked) {
    background-color: var(--body-background-color);
    color: var(--text-color);
  }
  .site-header .theme-picker label input {
    inset: 0;
    opacity: 0;
    position: absolute;
    z-index: 2;
  }
}

.identity {
  position: relative;
  z-index: 4;
}
.identity svg {
  aspect-ratio: 1;
  width: 3em;
}
@media (min-width: 64em) {
  .identity svg {
    width: 4.125em;
  }
}
.identity svg path {
  fill: var(--brand-beige);
}
[data-theme=light] .identity svg path {
  fill: var(--brand-beige);
}
@media (prefers-color-scheme: dark) {
  .identity svg path {
    fill: var(--brand-blue);
  }
}
[data-theme=dark] .identity svg path {
  fill: var(--brand-blue);
}
.identity a {
  border-bottom: none;
  display: flex;
  gap: var(--space-2xs);
}
.identity a:hover {
  border-bottom: none;
}
.identity a:focus-visible {
  outline: var(--brand-beige) dotted 2px;
  outline-offset: 8px;
}
@media (prefers-color-scheme: dark) {
  .identity a:focus-visible {
    outline-color: var(--brand-blue);
